function StationData() {
    this._type = 1;//数据类型：short型2字节。
    this._description = "";//观测数据描述信息，最多100字节
    this._level = 0;//层次：代表数据高度对应的压强值，float型4字节。
    this._levelDescription = "";//观测数据层次描述信息，最多50字节
    this._year;//观测日期：年：采用完整的整数表示，如2014，int型4字节。
    this._month;//观测日期：月：int型4字节。
    this._day;//观测日期：日：int型4字节。
    this._hour;//观测时：采用24小时制，数值范围0-23，int型4字节。
    this._minute = 0;//观测分：0-59,int型4字节。
    this._second = 0;//观测秒：0-59，int型4字节。
    //不要忘记还继承了超类MeteorologicalData的timezone，int型4字节，表示时区
    //数据头最后是100字节的扩展段Extent,用于未来扩展，不用时建议设置为全0字节。扩展段后紧接数据区。

    //数据区开始
    /*
    数据区由2大部分组成：
    第一大部分：1个int，表示站点数目
    第二大部分：该数据区中实际存储的所有物理量ID个数，以及物理量ID和类型的映射：物理量ID个数：short 2字节，物理量ID 2字节，物理量数值类型(short 2字节)，物理量ID和物理量数值类型4字节重复存放
    物理量数值类型: 1: byte, 2: short, 3: int, 4: long, 5: float, 6: double, 7: string
    第三大部分由各站点记录拼接而成：14个字节作为记录开头: 站点ID(int 4字节), 站点经度(float 4字节), 站点纬度(float 4字节), 站点物理量个数(除去经纬度不算,short 2字节)
    然后是各物理量数据信息: 物理量ID(short 2字节), 物理量值(字节数受物理量数值类型决定)
    */
    //private Map < Integer, Map < Integer, String > > stationDataMap;//数据的实际数据存储结构 <站点ID,<物理量ID，物理量值>>,是一个包含了所有站点所有物理量信息的Map
    this._stationDataMap = {};

}
