
    /**
    * 表示图像（遥感图像）存储格式BSQ/BIL/BIP
    * @property {enum} BIP  按波段保存，也就是一个波段保存后接着保存第二个波段
    * @property {enum} BSQ  BIP是按像元保存，即先保存第一个波段的第一个像元，之后保存第二波段的第一个像元，依次保存
    * @property {enum} BIL   按行保存，就是保存第一个波段的第一行后接着保存第二个波段的第一行，依次类推
    *@memberof MeteoLib.Data.RasterData
    */
    var enumInterleave =
	{
	    BIP: 0,
	    BSQ: 1,
	    BIL: 2
	}
    module.exports= enumInterleave;
