/**
 * 本类用于在cesium中绘制常用气象符号
 * 作者：吴厚波
 * 更新时间：2018/07/14
 */

/** 
*创建常用气象符号类
*@memberof MeteoLib.Util
*@constructor
 */
function MeteoSign() {
    this.color = 'rgba(0,0,255,1)';
    this.size = 100; //正方形面积边长
    this.lineWidth = 4;
}

/** 初始化参数
 * @param {object} options 参数
 * @param {Viewer} options.viewer cesium绘图对象
 * @param {int} [options.size] 绘制的正方形区域边长
 * @param {string} [options.color] 颜色代码
 */
MeteoSign.prototype.Initialize = function (options) {
    this.earth.viewer = options.viewer;
    if (options.color)
        this.color = options.color;
    else
        this.color = 'dark';
    if (options.size)
        this.size = options.size;
    else
        this.size = 100;
}

/**
 * 绘制气象标志
 * @param {int} options.type 标志类型,0为风;1为雨;2为雪；3为雾；4为云；5为风沙；6为其他。
 * @param {array} options.tags 标志信息数组。其中每个tag对象应包含经度lon、纬度lat、速度speed、角度angle、类型tag.type属性
 * 其中速度speed仅'风'需要，且此时无需类型tag.type变量，会根据速度自动分类。当值为-1时为旋转风
 * 当type==1时，tag.type为雨类型，值为，0：小雨；1：中雨；2：大雨；3：暴雨；4：大暴雨；5：特大暴雨；6：阵雨；7：轻冻雨；8：冻雨
 * 当type==2时，tag.type为雪类型，值为，0：雨雪；1：小雪；2：中雪；3：大雪；4：暴雪；5：阵雪；6：霜冻
 * 当type==3时，tag.type为雾类型，值为，0：轻雪；1：雾
 * 当type==4时，tag.type为云类型，值为，0：晴天；1：多云；2：阴天
 * 当type==5时，tag.type为风沙类型，值为，0：浮尘；1：扬沙；2：轻沙暴；3：沙暴
 * 当type==6时，tag.type为其他类型，值为，0：雷暴；1：冰雹；2：台风；3：烟；4：霾；5：冷暖中心N；6：冷暖中心L；5：高低中心G；5：高低中心D。
 * @param {int} options.len 数组长度
 */
MeteoSign.prototype.ShowTag = function (options) {
    for (var i = 0; i < options.len; i++) {
        var tag = options.tags[i];
        switch (options.type) {
            case 0: //生成风羽
                canvas = this.GetWindCanvas(tag.speed);
                break;
            case 1: //生成雨
                canvas = this.GetRainCanvas(tag.type);
                break;
            case 2: //生成雪
                canvas = this.GetSnowCanvas(tag.type);
                break;
            case 3: //生成雾
                canvas = this.GetFogCanvas(tag.type);
                break;
            case 4: //生成云
                canvas = this.GetCloudCanvas(tag.type);
                break;
            case 5: //生成风沙
                canvas = this.GetSandCanvas(tag.type);
                break;
            case 6: //生成其他
                canvas = this.GetOtherCanvas(tag.type);
                break;
        }
        if (!tag.angle)
            tag.angle = 0;
        //将标志加载至地图上
        var citizensBankPark = viewer.entities.add({
            position: Cesium.Cartesian3.fromDegrees(tag.lon, tag.lat),
            //广告牌
            billboard: {
                image: canvas, //可以是canvas图像,也可以是图像地址或链接
                width: this.size,
                height: this.size,
                rotaion: tag.angle
            },
        });

        // //将雨标加载至地图上
        // str = 'flame' + i;
        // var redRectangle = viewer.entities.add({
        //     name: str,
        //     rectangle: {
        //         coordinates: Cesium.Rectangle.fromDegrees(tag.lon, tag.lat, tag.lon + this.size * 2, tag.lat + this.size),
        //         material: canvas,
        //         rotaion: tag.angle
        //     }
        // });
    }
};

/**
 * 绘制气象线
 * @param {int} options.type 线的类型,0为直线;1为贝塞尔曲线
 * @param {array} options.points 线所经过经纬度坐标数组，组内数据为如[-76.091308, 18.427501]的长度2的数组，前者为经度，后者为纬度
 * @param {int} [options.lineColor] 线的颜色，为cesium颜色，默认为Cesium.Color.WHITE
 * @param {int} [options.lineWidth] 线的宽度，默认为5
 * @param {int} [options.text] 末尾文字，为空时不加
 * @param {int} [options.font] 文字字体，默认为"30px sans-serif"
 * @param {int} [options.textColor] 文字颜色，默认与线同色
 */
MeteoSign.prototype.ShowLine = function (options) {
    if (!options.lineColor)
        options.lineColor = Cesium.Color.WHITE;
    if (!options.lineWidth)
        options.lineWidth = 5;
    switch (options.type) {
        case 0: //直线
            coords = options.points;
            break;
        case 1: //贝塞尔曲线
            {
                line = turf.lineString(options.points);
                var curved = turf.bezierSpline(line);
                var coords = turf.getCoords(curved);
            }
            break;
    }
    data = [];
    for (var i = 0; i < coords.length; i++) {
        data.push(coords[i][0]);
        data.push(coords[i][1]);
    }
    var redLine = viewer.entities.add({
        name: 'line',
        polyline: {
            positions: Cesium.Cartesian3.fromDegreesArray(data),
            width: options.lineWidth,
            material: options.lineColor,
        }
    });
    if (options.text) {

        if (!options.font)
            options.font = "30px sans-serif";
        if (!options.textColor)
            options.textColor = options.lineColor;
        var lableEntity = viewer.entities.add({
            position: Cesium.Cartesian3.fromDegrees(coords[coords.length - 1][0], coords[coords.length - 1][1]),
            label: {
                text: options.text,
                fillColor: options.textColor,
                font: options.font
            }
        });
    }

};

/**
 * 绘制风的canvas图
 * @param {float} speed 风速，单位m/s。根据风速生成对应等级风羽，当为-1时，生成旋转风标
 * @return {Canvas}
 */
MeteoSign.prototype.GetWindCanvas = function (speed) {
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //设置样式
    context.lineWidth = this.lineWidth ? this.lineWidth : this.size / 40;
    context.strokeStyle = this.color;

    var x = context.lineWidth / 2;
    var y = this.size / 4;
    //设置对象起始点和终点
    context.moveTo(x, y); //主干起始
    context.lineTo(x, this.size); //主干结束

    //以高度的1/4为长风羽
    var disY = Math.floor((this.size - y) / 7); //每个风羽间的间隔
    var longPlume = (this.size - y) / 2; //长风羽长度
    var angle = 70; //风羽角度
    var nowX = x;
    var nowY = y + context.lineWidth / 2;
    context.moveTo(nowX, nowY);

    if (speed == -1) {
        context.lineTo(nowX + longPlume, nowY);
        context.lineTo(nowX, nowY + longPlume);
        nowX = x + longPlume * 1.5;
        nowY = y;
        context.moveTo(nowX, nowY);
        context.lineTo(nowX, this.size - context.lineWidth / 2);
        context.lineTo(nowX - longPlume, this.size - context.lineWidth / 2);
        context.lineTo(nowX, this.size - longPlume - context.lineWidth / 2);
    } else {
        //根据风速绘制风羽
        var longNum = 0; //长风羽个数
        var shortNum = 0; //短风羽个数
        var triNum = 0; //三角个数
        //根据风速判断绘制结果
        //1级风
        if (speed <= 1.5) {
            shortNum = 1;
        }
        //2级风
        else if (speed <= 3.3) {
            longNum = 1;
        }
        //3级风
        else if (speed <= 5.4) {
            longNum = 1;
            shortNum = 1;
        }
        //4级风
        else if (speed <= 7.9) {
            longNum = 2;
        }
        //5级风
        else if (speed <= 10.7) {
            longNum = 2;
            shortNum = 1;
        }
        //6级风
        else if (speed <= 13.8) {
            longNum = 3;
        }
        //7级风
        else if (speed <= 17.1) {
            longNum = 4;
            shortNum = 1;
        }
        //8级风
        else if (speed <= 20.7) {
            triNum = 1;
            shortNum = 1;
        }
        //9级风
        else if (speed <= 24.4) {
            triNum = 1;
            longNum = 1;
            shortNum = 1;
        }
        //10级风
        else if (speed <= 28.4) {
            triNum = 1;
            longNum = 2;
            shortNum = 1;
        }
        //11级风
        else if (speed <= 32.6) {
            triNum = 1;
            longNum = 3;
            shortNum = 1;
        }
        //12级风
        else {
            triNum = Math.floor(speed / 20);
            longNum = Math.floor((speed - triNum * 20) / 4);
            shortNum = Math.floor((speed - triNum * 20 - longNum * 4) / 2);
        }

        //绘制风羽
        //三角风羽
        for (var i = 0; i < triNum; i++) {
            //三角风羽横向与长风羽一致，竖向占据一个间隔
            context.lineTo(nowX + longPlume * Math.sin(angle * Math.PI / 180), nowY);
            context.lineTo(nowX, nowY + disY);
            nowY += 2 * disY;
            context.moveTo(nowX, nowY);
        }
        //长风羽
        for (var i = 0; i < longNum; i++) {
            context.lineTo(nowX + longPlume * Math.sin(angle * Math.PI / 180), nowY - longPlume * Math.cos(angle * Math.PI / 180));
            nowY += disY;
            context.moveTo(nowX, nowY);
        }
        //短风羽
        for (var i = 0; i < shortNum; i++) {
            //短风羽长度为长风羽的1/2
            context.lineTo(nowX + longPlume / 2 * Math.sin(angle * Math.PI / 180), nowY - longPlume / 2 * Math.cos(angle * Math.PI / 180));
            nowY += disY;
            context.moveTo(nowX, nowY);
        }
    }

    //绘制
    context.stroke();

    return canvas;
}

/**
 * 绘制下雨的canvas图
 * @param {int} type 类型。0：小雨；1：中雨；2：大雨；3：暴雨；4：大暴雨；5：特大暴雨；6：阵雨；7：轻冻雨；8：冻雨
 * @return {Canvas}
 */
MeteoSign.prototype.GetRainCanvas = function (type) {
    //生成雨标
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");
    //设置样式
    context.fillStyle = this.color;
    radius = this.size / 7; //以1/7之一的高度为半径
    context.strokeStyle = this.color;
    //绘制不同雨的类型
    switch (type) {
        case 0: //小雨
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 1: //中雨
            {
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 2: //大雨
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 1.2 / Math.sin(60 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 1.2;
                y = this.size / 2 + radius * 1.2 * Math.tan(30 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 + radius * 1.2;
                y = this.size / 2 + radius * 1.2 * Math.tan(30 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 3: //暴雨
            {
                context.beginPath();
                x = this.size / 2 + radius * 1.5;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 1.5;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 + radius * 1.5;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 1.5;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 4: //大暴雨
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 + radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 + radius * 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 5: //特大暴雨
            {
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2 - radius * 2.2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2 + radius * 2.2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 6: //阵雨
            {
                radius = this.size / 6;
                context.lineWidth = radius / 5;
                context.beginPath();
                x = this.size / 2;
                y = radius;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                len = radius * 4;
                x -= len / 2;
                y += radius + context.lineWidth / 2;
                context.beginPath();
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.lineTo(x + len / 2, y + len * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.stroke();
            }
            break;
        case 7: //轻冻雨
            {
                radius = this.size / 5;
                radius2 = radius / 2.5;
                context.lineWidth = radius / 5;
                x = this.size / 2 - radius;
                y = this.size / 2;
                context.arc(x, y, radius, 1 * Math.PI, 2 * Math.PI);
                x += radius * 2;
                context.moveTo(x + radius, y);
                context.arc(x, y, radius, 0, 1 * Math.PI);
                context.stroke();

                context.beginPath();
                x = this.size / 2 - radius;
                y = this.size / 2 - (radius - context.lineWidth) / 2;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.fill();
            }
            break;
        case 8: //冻雨
            {
                radius = this.size / 5;
                radius2 = radius / 2.5;
                context.lineWidth = radius / 5;
                x = this.size / 2 - radius;
                y = this.size / 2;
                context.arc(x, y, radius, 1 * Math.PI, 2 * Math.PI);
                x += radius * 2;
                context.moveTo(x + radius, y);
                context.arc(x, y, radius, 0, 1 * Math.PI);
                context.stroke();

                context.beginPath();
                x = this.size / 2 - radius;
                y = this.size / 2 - (radius - context.lineWidth) / 2;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 2;
                y += radius - context.lineWidth;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
    }

    return canvas;
}

/**
 * 绘制下雪的canvas图
 * @param {int} type 类型。0：雨雪；1：小雪；2：中雪；3：大雪；4：暴雪；5：阵雪；6：霜冻
 * @return {Canvas}
 */
MeteoSign.prototype.GetSnowCanvas = function (type) {
    //生成雨标
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");
    //设置样式
    context.fillStyle = this.color;
    radius = this.size / 7; //以1/7之一的高度为半径
    context.strokeStyle = this.color;
    //绘制不同雪的类型
    switch (type) {
        case 0: //雨雪
            {
                len = this.size * 0.9;
                context.lineWidth = len / 20;
                x = (this.size - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - len / 2 * Math.sin(45 * Math.PI / 180);
                y = x;
                context.moveTo(x, y);
                context.lineTo(this.size - x, this.size - y);
                context.moveTo(this.size - x, y);
                context.lineTo(x, this.size - y);
                context.stroke();
                radius = len / 6;
                context.beginPath();
                context.arc(this.size / 2, this.size / 2 - radius * 2, radius, 0, 2 * Math.PI);
                context.fill();
            }
            break;
        case 1: //小雪
            {
                len = this.size * 0.9;
                context.lineWidth = len / 20;
                x = (this.size - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - len / 2 * Math.sin(45 * Math.PI / 180);
                y = x;
                context.moveTo(x, y);
                context.lineTo(this.size - x, this.size - y);
                context.moveTo(this.size - x, y);
                context.lineTo(x, this.size - y);
                context.stroke();
            }
            break;
        case 2: //中雪
            {
                len = this.size / 2 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 3: //大雪
            {
                len = this.size / 2 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 4: //暴雪
            {
                len = this.size / 3 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 5: //阵雪
            {
                len = this.size / 3 * 0.9;
                context.lineWidth = len / 20;
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 2 - len / 2;
                y = this.size / 4 - len / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y = y - height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                trilen = len * 2.5;
                x = this.size / 2 - trilen / 2;
                y = len + context.lineWidth / 2;
                context.beginPath();
                context.moveTo(x, y);
                context.lineTo(x + trilen, y);
                context.lineTo(x + trilen / 2, y + trilen * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.stroke();
            }
            break;
        case 6: //霜冻
            {
                len = this.size;
                context.lineWidth = len / 15;
                x = context.lineWidth / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x, this.size - context.lineWidth / 2);
                context.lineTo(this.size - context.lineWidth / 2, this.size - context.lineWidth / 2);
                context.lineTo(this.size - context.lineWidth / 2, y);
                context.stroke();
            }
            break;
    }

    return canvas;
}

/**
 * 绘制雾的canvas图
 * @param {int} type 类型。0：轻雪；1：雾
 * @return {Canvas}
 */
MeteoSign.prototype.GetFogCanvas = function (type) {
    //生成雨标
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //设置样式
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    len = this.size; //横线长度
    context.lineWidth = len / 15;
    //绘制不同雾的类型
    switch (type) {
        case 0: //轻雾
            {
                x = 0;
                y = this.size / 3;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 3;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.stroke();
            }
            break;
        case 1: //雾
            {
                x = 0;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.stroke();
            }
            break;
    }

    return canvas;
}

/**
 * 绘制云的canvas图
 * @param {int} type 类型。0：晴天；1：多云；2：阴天
 * @return {Canvas}
 */
MeteoSign.prototype.GetCloudCanvas = function (type) {
    //生成雨标
    var canvas = document.createElement('canvas');
    var context = canvas.getContext("2d");
    canvas.width = this.size;
    canvas.height = this.size;

    //设置样式
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    radius = this.size / 3;
    context.lineWidth = this.size / 15;
    x = this.size / 2;
    y = this.size / 2;
    //绘制不同云的类型
    switch (type) {
        case 0: //晴
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
            }
            break;
        case 1: //多云
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
                context.beginPath();
                context.arc(x, y, radius, -Math.PI / 2, Math.PI / 2);
                context.closePath();
                context.fill();
            }
            break;
        case 2: //阴天
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.fill();
            }
            break;
    }

    return canvas;
}

/**
 * 绘制风沙的canvas图
 * @param {int} type 类型。0：浮尘；1：扬沙；2：轻沙暴；3：沙暴
 * @return {Canvas}
 */
MeteoSign.prototype.GetSandCanvas = function (type) {
    //生成雨标
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //设置样式
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    //绘制不同风沙的类型
    switch (type) {
        case 0: //浮尘
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();
            }
            break;
        case 1: //扬沙
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius * 0.75;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();

                context.beginPath();
                y = this.size;
                context.moveTo(x, y);
                y = this.size * 0.12;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                context.moveTo(x - radius / 2, y);
                context.lineTo(x + radius / 2, y);
                context.lineTo(x, y - radius / 2 * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.fill();
            }
            break;
        case 2: //轻沙暴
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();

                context.lineWidth = this.size / 50;
                context.beginPath();
                y = this.size / 2 - context.lineWidth * 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                y = this.size / 2 + context.lineWidth * 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                y = this.size / 2;
                context.moveTo(x, y - radius / 2);
                context.lineTo(x, y + radius / 2);
                context.lineTo(x + radius / 2 * Math.sin(60 * Math.PI / 180), y);
                context.closePath();
                context.fill();

            }
            break;
        case 3: //沙暴
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();
                context.beginPath();
                y = this.size / 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                context.moveTo(x, y - radius / 2);
                context.lineTo(x, y + radius / 2);
                context.lineTo(x + radius / 2 * Math.sin(60 * Math.PI / 180), y);
                context.closePath();
                context.fill();
            }
            break;
    }

    return canvas;
}


/**
 * 绘制其他的canvas图
 * @param {int} type 类型。0：雷暴；1：冰雹；2：台风；3：烟；4：霾；5：冷暖中心N；6：冷暖中心L；7：高低中心G；8：高低中心D。
 * @param {Cesium.Color|String}color
 * @return {Canvas}
 */
MeteoSign.prototype.GetOtherCanvas = function (type, color) {
    if (color instanceof Cesium.Color) {
        color = color.toCssColorString();
    }
    //生成雨标
    var canvas = document.createElement('canvas');
    var context = canvas.getContext("2d");
    canvas.width = this.size;
    canvas.height = this.size;
    //设置样式
    context.fillStyle = color ? color : this.color;
    context.strokeStyle = color ? color : this.color;
    //绘制不同风沙的类型
    switch (type) {
        case 0: //雷暴
            {
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size * 0.1;
                context.moveTo(x - this.size / 10, y);
                context.lineTo(x + this.size / 10, y);
                context.stroke();
                x = this.size / 4;
                y += y;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                x += this.size / 2;
                context.lineTo(x, y);
                len = this.size / 3;
                context.lineTo(x - len, y + len);
                y += len * 2;
                context.lineTo(x, y);
                context.stroke();
                context.beginPath();
                context.moveTo(x, y);
                len = len / 3 * Math.sin(45 * Math.PI / 180);
                context.lineTo(x + len, y - len);
                context.lineTo(x + len, y + len);
                context.lineTo(x - len, y + len);
                context.closePath();
                context.fill();
            }
            break;
        case 1: //冰雹
            {
                context.lineWidth = this.size / 15;
                context.beginPath();
                len = this.size * 2 / 3;
                x = this.size / 2;
                y = this.size / 2 - context.lineWidth;
                context.moveTo(x - len / 2, y);
                context.lineTo(x + len / 2, y);
                context.lineTo(x, y - len / 2);
                context.closePath();
                context.stroke();
                context.beginPath();
                y += context.lineWidth * 2;
                context.moveTo(x - len / 2, y);
                context.lineTo(x + len / 2, y);
                context.lineTo(x, y + len / 2);
                context.closePath();
                context.stroke();
            }
            break;
        case 2: //台风
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 10;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
                context.lineWidth = 5;
                context.beginPath();
                radius2 = radius + context.lineWidth / 2;
                context.moveTo(x - radius2, y);
                context.arc(x + radius2, y, radius2 * 2 + context.lineWidth * 2, Math.PI, Math.PI * 3 / 2);
                context.arc(x + radius2, y - radius2 - context.lineWidth * 2, radius, Math.PI * 3 / 2, Math.PI, true);
                context.closePath();
                context.fill();
                context.beginPath();
                context.moveTo(x + radius2, y);
                context.arc(x - radius2, y, radius2 * 2 + context.lineWidth * 2, 0, Math.PI / 2);
                context.arc(x - radius2, y + radius2 + context.lineWidth * 2, radius, -Math.PI * 3 / 2, 0, true);
                context.closePath();
                context.fill();
            }
            break;
        case 3: //烟
            {
                context.lineWidth = this.size / 15;
                x = context.lineWidth / 2;
                y = this.size / 3;
                radius = (this.size - y) / 4;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                context.arc(x + radius, y, radius, Math.PI, 2 * Math.PI, false);
                context.arc(x + radius * 3, y, radius, Math.PI, 2 * Math.PI, false);
                context.stroke();
            }
            break;
        case 4: //霾
            {
                context.lineWidth = this.size / 15;
                radius = this.size / 5;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x - radius, y, radius, 0, 2 * Math.PI);
                context.moveTo(x + radius * 2, y);
                context.arc(x + radius, y, radius, 0, 2 * Math.PI);
                context.stroke();
            }
            break;
        case 5: //冷暖中心N
            {
                context.lineWidth = this.size / 10;
                x = this.size / 4;
                y = context.lineWidth / 2;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                context.lineTo(x + this.size / 2, this.size);
                context.lineTo(x + this.size / 2, 0);
                context.stroke();
            }
            break;
        case 6: //冷暖中心L
            {
                context.lineWidth = this.size / 10;
                x = this.size / 4;
                y = this.size - context.lineWidth / 2;
                context.moveTo(x, 0);
                context.lineTo(x, y);
                context.lineTo(x + this.size / 2, y);
                context.stroke();
            }
            break;
        case 7: //高低中心G
            {
                context.lineWidth = this.size / 10;
                radius = this.size / 2 - context.lineWidth / 2;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(this.size / 2, this.size / 2, radius, Math.PI / 5, Math.PI * 7 / 4, false);
                context.stroke();
                context.moveTo(x + radius / 3, y);
                x += radius * 4 / 5;
                context.lineTo(x, y);
                context.lineTo(x, this.size);
                context.stroke();
            }
            break;
        case 8: //高低中心D
            {
                context.lineWidth = this.size / 10;
                radius = this.size / 2 - context.lineWidth / 2;
                x = this.size / 4;
                y = this.size / 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI / 2);
                context.moveTo(x, 0);
                context.lineTo(x, this.size);
                context.stroke();
            }
            break;
    }


    return canvas;
}

module.exports = MeteoSign;
