var GridDataColorMap = require('./GridDataColorMap');

/**
 * 色斑图生成工具类
 * @param {Object} options
 * @param {String} options.name 名称 
 * @param {String} [options.palattePath] micaps图例xml文件路径，palattePath和colorMap不能同时为空
 * @param {GridDataColorMap} [options.colorMap] 默认图例，palattePath和colorMap不能同时为空
 * @param {Boolean} [options.selectColorByIndex=false] 为true表示绘图时给定的数值为图例（调色板）中的索引，将直接从图例数组中取颜色；否则为图例中的数值，取颜色时先将给定数值与图例中的数值范围进行对比，落在指定区间则取该区间的对应的颜色。
 * @param {Boolean} [options.palatteReverse=false] 从micaps图例xml文件解析图例时，是否需要逆转列表，micaps图例有的时按数值从大到小排列，有的从小到大排列，需要根据实际情况调整此参数。
 * @param {Number} [options.quality=1] 输出jpg图像质量，0——1之间，质量越好图像文件越大。
 * @constructor
 * @memberof MeteoLib.Render
 */
function RasterImageGenerator(options) {
    this.name = options.name ? options.name : options.palattePath;

    this.selectColorByIndex = options.selectColorByIndex;
    this.palattePath = options.palattePath;
    this.palatteReverse = options.palatteReverse;
    this.quality = options.quality ? options.quality : 1;

    /**
     * @type {boolean}
     */
    this.ready = false;
    /**
     * @type {Promise<boolean>}
     */
    this.readyPromise = Cesium.when.defer();
    /**
     * @type {Array|MeteoLib.Render.GridDataColorMap}
     */
    this.colorMap = options.colorMap;

    var that = this;
    if (this.colorMap) {
        this.readyPromise.resolve(that);
    } else if (RasterImageGenerator.ColorMaps[this.palattePath]) {
        this.colorMap = RasterImageGenerator.ColorMaps[this.palattePath];
        this.readyPromise.resolve(that);
    } else {
        GridDataColorMap.fromMICAPSPalatteXml(this.palattePath).then(function (colorMap) {
            if (that.palatteReverse) {
                colorMap.reverse();
            }
            RasterImageGenerator.ColorMaps[that.palattePath] = colorMap;
            that.colorMap = RasterImageGenerator.ColorMaps[that.palattePath];
            that.ready = true;
            that.readyPromise.resolve(that);
        }).otherwise(function (err) {
            that.readyPromise.reject(err);
        });
    }
    if (typeof options.generate == 'function') {
        this.generate = options.generate;
    }

    for (var i in options) {
        if (!options.hasOwnProperty(i) || this.hasOwnProperty(i)) {
            continue;
        }
        this[i] = options[i];
    }
    RasterImageGenerator.register(this);
}
/**
 * 存储全局图例，避免重复解析或者加载
 */
RasterImageGenerator.ColorMaps = {};

RasterImageGenerator.Providers = [];
/**
*注册
*/
RasterImageGenerator.register = function (options) {

    for (var i = 0; i < RasterImageGenerator.Providers.length; i++) {
        if (options.name == RasterImageGenerator.Providers[i].name) {
            return false;
        }
    }

    if (!(options instanceof RasterImageGenerator)) {
        options = new RasterImageGenerator(options);
    }
    RasterImageGenerator.Providers.push(options);
    return options;
}
/**
*
*/
RasterImageGenerator.find = function (name) {
    for (var i = 0; i < RasterImageGenerator.Providers.length; i++) {
        var provider = RasterImageGenerator.Providers[i];
        if (provider.name == name) {
            return provider;
        }
    }
    return undefined;
}


/**
 *  
 * @param {Array.<Number>} data 网格数据
 * @param {Number} width 图像宽度
 * @param {Number} height 图像高度
 * @param {Array.<Number>} [calTable] 定标查找表
 * @param {Array.<Number>} [bbox]
 * @return {Canvas|MeteoLib.Util.Scijs.JPEG}
 */
RasterImageGenerator.prototype.generate = function (data, width, height, calTable, bbox) {
    if (typeof document == 'undefined' || typeof Blob == 'undefined') {
        return this.generateNode(data, width, height, calTable, bbox);
    }
    var pixels = new Uint8ClampedArray(width * height * 4);

    var color = null;
    for (var i = 0; i < data.length; i++) {
        var value = data[i];
        if (calTable) {
            value = calTable[value];
        }
        if (this.selectColorByIndex) {
            if (this.colorMap[value])
                color = this.colorMap[value][2];
        } else {
            color = GridDataColorMap.getColor(value, this.colorMap);
        }

        if (color != null) {
            pixels[i * 4] = color[0];
            pixels[i * 4 + 1] = color[1];
            pixels[i * 4 + 2] = color[2];
            pixels[i * 4 + 3] = color[3];
        }
    }


    var cv = document.createElement('canvas');
    cv.width = width;
    cv.height = height;
    var ctx = cv.getContext('2d');
    var imgData = ctx.getImageData(0, 0, width, height);
    imgData.data.set(pixels);
    ctx.putImageData(imgData, 0, 0);
    pixels = [];
    return cv;
}

/**
 *  
 * @param {Array.<Number>} data 卫星云图数据
 * @param {Number} width 图像宽度
 * @param {Number} height 图像高度
 * @param {Array.<Number>} [calTable] 定标查找表
 * @param {Array.<Number>} [bbox]
 * @return {MeteoLib.Util.Scijs.JPEG}
 */
RasterImageGenerator.prototype.generateNode = function (data, width, height, calTable) {
    var Scijs = require('../Util/Sci');
    var JPEG = Scijs.jpegJs.JPEG;
    
    var jpg = new JPEG({
        width: width,
        height: height
    })
    var color = null;
    for (var i = 0; i < data.length; i++) {
        var value = data[i];
        if (calTable) {
            value = calTable[value];
        }
        if (this.selectColorByIndex) {
            color = this.colorMap[value][2];
        } else {
            color = GridDataColorMap.getColor(value, this.colorMap);
        }

        if (color != null) {
            jpg.data[i * 4] = color[0];
            jpg.data[i * 4 + 1] = color[1];
            jpg.data[i * 4 + 2] = color[2];
            jpg.data[i * 4 + 3] = color[3];
        }
    }
    jpg = JPEG.encode(jpg, this.quality * 100);//转成JPEG文件格式
    return jpg;
}
/**
*
*/
RasterImageGenerator.prototype.clone = function () {
    return new RasterImageGenerator(this);
}
