/**
*@class
*@memberof MeteoLib.Util
*/
function Path() { }
/**
*
*获取文件扩展名（后缀）
*@param {String}fname 文件名
*/
Path.GetExtension = function (fname) {
    var start = fname.lastIndexOf(".");
    if (start >= 0) {
        return fname.substring(start, fname.length);
    }
    return "";
}

/**
*
*获取文件扩展名（后缀）
*@param {String}fname 文件名
*/
Path.GetFileName = function (fname) {
    var start = fname.lastIndexOf("/");
    if (start < 0) {
        return fname;
    }
    return fname.substring(start + 1, fname.length);
}
/**
*
*获取文件夹
*@param {String}fname 文件名
*/
Path.GetDirectoryName = function (fname) {
    var start = fname.lastIndexOf("/");
    if (start < 0) {
        return "";
    }
    return fname.substring(0, start);
}
/**
*
*合并文件目录和文件名
*@param {String}dir 文件目录
*@param {String}fname 文件名
*@return {String}
*/
Path.Combine = function (dir, fname) {
    return dir + fname;
}
/**
*拼接路径
*@param {Array.<String>|Any}arguments
*@return {String}
*/
Path.join = function () {
    var paths = [];
    var srcArguments = arguments;
    if (srcArguments.length == 1 && Array.isArray(srcArguments[0])) {
        srcArguments = srcArguments[0];
    }
    srcArguments.forEach(function (path) {
        if (path.length > 1 && path[path.length - 1] == '/') {
            path = path.substr(0, path.length - 1);
        }
        paths.push(path)
    })
    return paths.join('/');
}

/**
*
*@param {String} fname
*@param {String} newExt
*/
Path.ChangeExtension = function (fname, newExt) {
    return fname.replace(Path.GetExtension(fname), newExt);
}

/**
     * 对路径进行标准化
     * @param {String}path
     * @return {String}path
     */
Path.normalizePath = function (path) {
    //将"\"替换为"/"，将连续多个"/"替换为一个"/"，将一个或空格替换"_",为去掉尾部的"/"
    if (path == null)//健壮性
        return "";

    path = path.replace(/\\\\+/g, "/");//将一个或多个\替换为一个/
    path = path.replace(/\/{2,}/g, "/");//将2个或2个以上的/替换为一个/
    path = path.replace(/\s+/g, "_");//将1个或多个空格替换为下划线
    if (path.endsWith("."))//如果结尾是"."，则去掉，因为File.getParentFile().getAbsolutePath()以"."结尾
        path = path.substring(0, path.length() - 1);//去掉字符串结尾的"/"
    if (path.endsWith("/"))//如果结尾是"/"，执行至此只可能有一个结尾/
        path = path.substring(0, path.length() - 1);//去掉字符串结尾的"/"
    return path;
}

//    return Path;
//});
/**
 * 
 * @param {String} fname
 * @return {String} 
 */
Path.GetFileNameWithoutExtension = function (fname) {
    fname = Path.GetFileName(fname);
    fname = Path.ChangeExtension(fname, "");
    return fname;
}

module.exports = Path;