var Path = require('../../Util/Path');

/**
 * 该类为所有气象数据类的抽象基类
 * @constructor
 * @memberof MeteoLib.Data.Micaps
 * @property {String}familyName 该气象数据的数据类型标识,如T639, SATELLITE
 * @property {String}dataPath 该气象数据的路径表示，可能为虚拟URL，如Cassandra的CF+Row Key，或者Samba服务器的相对路径。该路径由setDataPath方法设定，通常在解码时由Decoder的具体类指定。
 * @property {String}dataName 该气象数据的数据名称，可能为虚拟文件名，如Cassandra中的Column Name,或者Samba服务器的文件名，该名称由setDataName方法设定，通常在解码时由Decoder的具体类指定。
 * @property {String}originalAbsoluteDataName 该数据原始FTP接收时的绝对文件名称
 * @property {Number}timezone 该气象数据的URL表示中的时间对应的时区，由于该类的对象是最终客户端用户使用的数据，因此通常需要将此实例变量设置为客户端所在时区。取值范围：[-12,12]的整数值，其中-12和12都代表东西十二区，0代表中时区，[1,11]代表东一区至东十一区，[-11,-1]代表西十一区至西一区。该变量通常在解码时由具体的Decoder设定
 */
function MeteorologicalData() {

    this._familyName = "";//该气象数据的数据类型标识,如T639, SATELLITE
    this._dataPath = "";//该气象数据的路径表示，可能为虚拟URL，如Cassandra的CF+Row Key，或者Samba服务器的相对路径。该路径由setDataPath方法设定，通常在解码时由Decoder的具体类指定。
    this._dataName = "";//该气象数据的数据名称，可能为虚拟文件名，如Cassandra中的Column Name,或者Samba服务器的文件名，该名称由setDataName方法设定，通常在解码时由Decoder的具体类指定。
    this._timezone = 8;//该气象数据的URL表示中的时间对应的时区，由于该类的对象是最终客户端用户使用的数据，因此通常需要将此实例变量设置为客户端所在时区。取值范围：[-12,12]的整数值，其中-12和12都代表东西十二区，0代表中时区，[1,11]代表东一区至东十一区，[-11,-1]代表西十一区至西一区。该变量通常在解码时由具体的Decoder设定
    this._originalAbsoluteDataName = ""; //该数据原始FTP接收时的绝对文件名称
}

Object.defineProperties(MeteorologicalData.prototype, {
    familyName: {
        get: function () {
            return this._familyName;
        }, set: function (val) {
            if (!val) val = "";

            val = val.replace(/\s+/g, "_");//将1个或多个空格替换为下划线
            this._familyName = val;
        }
    },
    dataName: {
        get: function () {
            return this._dataName;
        }, set: function (val) {
            this._dataName = val;
        }
    },
    dataPath: {
        get: function () {
            return this._dataPath;
        }, set: function (val) {
            val = Path.normalizePath(val);
            this._dataPath = val;
        }
    },
    timezone: {
        get: function () {
            return this._timezone;
        }, set: function (val) {
            this._timezone = val;
        }
    },
    originalAbsoluteDataName: {
        get: function () {
            return this._originalAbsoluteDataName;
        }, set: function (val) {
            if (!val) val = "";
            val = val.replace(/\\\\+/g, "/");//将一个或多个\替换为一个/
            val = val.replace(/\/{2,}/g, "/");//将2个或2个以上的/替换为一个/
            this._originalAbsoluteDataName = val;
        }
    },
    absoluteDataName: {
        get: function () {
            //返回"绝对路径文件名"为保证跨平台，统一采用/进行分隔
            return this.dataPath + "/" + this.dataName();
        }
    }
})

MeteorologicalData.serialVersionUID = 1;

