/**
 * 
 * @param {Int|Number} i
 * @return {Array.<Number>|Int8Array}
 */
MeteorologicalData.toLittleEndian = function (i) {
    //将4字节整数的Java大端字节序转换为小端字节序
    var b = new Int8Array(4);
    b[0] = (i & 0xff);
    b[1] = (i >> 8 & 0xff);
    b[2] = (i >> 16 & 0xff);
    b[3] = (i >> 24 & 0xff);
    return b;
}
