/**
 * 
 * @param {Int|Number} i
 * @return {Array.<Number>|Int8Array}
 */
MeteorologicalData.toLittleEndian = function (i) {
    //将2字节short整数的Java大端字节序转换为小端字节序
    var b = new Int8Array(2);
    b[0] = (i & 0xff);
    b[1] = (i >> 8 & 0xff);
    return b;
}

module.exports = MeteorologicalData;
