var defined=require('./defined');
var defineProperties=require('./defineProperties');
var enumDataType=require('./enumDataType');
var DataTypeHelper=require('./DataTypeHelper');

    /**
    *  栅格波段类，定义栅格波段数据结构及相关操作方法（如读取、统计等）接口
    *@property {Number}BandNo 波段号
    *@property {MeteoLib.Data.RasterData.RasterDataProvider}RasterDataProvider  数据提供者
    *@property {String}Description 描述
    *@property {Number}MeasureBits  
    *@property {MeteoLib.Data.RasterData.enumDataType}DataType = MeteoLib.Data.RasterData.enumDataType.UInt16 数据类型
    *@property {Number}DataTypeSize  DataType存储大小，即存储一个该数据类型所占用字节数
    *@property {Number}Width 波段宽度
    *@property {Number}Height  波段高度
    *@property {Number}ResolutionX  x向（一般为经向）分辨率
    *@property {Number}ResolutionY  x向（一般为纬向）分辨率
    *@property {Number}NoDataValue 无效值
    *@property {Number}DataOffset 偏移量
    *@property {Number}DataScale  缩放倍数
    *@property {Number}MinByMeasureBits  
    *@property {Number}MaxByMeasureBits  
    *@constructor
    *@memberof MeteoLib.Data.RasterData
    *@see MeteoLib.Data.Ldf.LdfRasterBand
   *@see MeteoLib.Data.Envi.EnviRasterBand
    */
    function RasterBand() {
        this._rasterDataProvider;
        //this._attributes = new AttributeManager();
        this._bandNo;
        this._description;
        this._measureBits;
        this._dataType = enumDataType.Unknow;
        this._dataTypeSize;
        this._width;
        this._height;
        this._noDataValue;
        this._dataOffset;
        this._dataScale;
        //		protected ISpatialReference _spatialRef;
        //		protected ICoordTransform _coordTransofrm;
        this._minByMeasureBits;
        this._maxByMeasureBits;
        this._resolutionX;
        this._resolutionY;
        //		protected CoordEnvelope _coordEnvelope;
        //		protected object _stretcher;
        if (typeof SDK !== 'undefined') {//供开发版代码提示用
            this.BandNo = 1;
            this.RasterDataProvider = this._rasterDataProvider;
            this.Description = "";
            this.MeasureBits = -1;
            this.DataType = enumDataType.UInt16;
            this.DataTypeSize = 2;
            this.Width = 500;
            this.Height = 500;
            this.ResolutionX = 0.01;
            this.ResolutionY = 0.01;
            this.NoDataValue = 65535;
            this.DataOffset = 0;
            this.DataScale = 0;
            this.MinByMeasureBits = 0;
            this.MaxByMeasureBits = 0;
        }
    }
    defineProperties(RasterBand.prototype, {
        BandNo: {
            get: function () {
                return this._bandNo;
            },
            set: function (value) {
                this._bandNo = value;
            }
        },
        RasterDataProvider: {
            get: function () {
                return this._rasterDataProvider;
            }
        },
        Description: {
            get: function () {
                return this._description;
            },
            set: function (value) {
                this._description = value;
            }
        },
        MeasureBits: {
            get: function () {
                return this._measureBits;
            },
            set: function (value) {
                this._measureBits = value;
            }
        },
        DataType: {
            get: function () {
                return this._dataType;
            }
        },
        DataTypeSize: {
            get: function () {
                if (this._dataTypeSize == 0) {
                    this._dataTypeSize = DataTypeHelper.SizeOf(this._dataType);
                }
                return this._dataTypeSize;
            }
        },
        Width: {
            get: function () {
                return this._width;
            }
        },
        Height: {
            get: function () {
                return this._height;
            }
        },
        ResolutionX: {
            get: function () {
                return this._resolutionX;
            }
        },
        ResolutionY: {
            get: function () {
                return this._resolutionY;
            }
        },
        NoDataValue: {
            get: function () {
                return this._noDataValue;
            }
        },
        DataOffset: {
            get: function () {
                return this._dataOffset;
            }
        },
        DataScale: {
            get: function () {
                return this._dataScale;
            }
        },
        MinByMeasureBits: {
            get: function () {
                return this._minByMeasureBits;
            }
        },
        MaxByMeasureBits: {
            get: function () {
                return this._maxByMeasureBits;
            }
        }
    });
    RasterBand.prototype.CheckArgumentsisValid = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize) {
        /// <summary>内部调用</summary>

        if (xOffset < 0 || yOffset < 0 || xOffset + xSize > this._width || yOffset + ySize > this._height) {
            throw new Error("请求的数据块(OffsetX = " + xOffset + ",OffsetY = " + yOffset + ",XSize =" + xSize + ",YSize = " + ySize + ")超过栅格范围！");
        }
        if (xBufferSize == 0 || yBufferSize == 0) {
            throw new Error("指定的缓存区为空！");
        }
    }
    /**
    *读取波段数据
    *@param {Number}xOffset x偏移量
    *@param {Number}xOffset y偏移量
    *@param {Number}xSize 待读取数据x方向宽度
    *@param {Number}ySize 待读取数据y方向宽度
    *@param {TypeArray}xOffset 保存读取结果的数组
    *@param {MeteoLib.Data.RasterData.enumDataType}dataType 数据类型
    *@param {Number}xBufferSize 保存读取结果的数组buffer的x方向宽度
    *@param {Number}yBufferSize 保存读取结果的数组buffer的y方向宽度
    */
    RasterBand.prototype.Read = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize) {
        /// <summary>读取波段数据</summary>
        /// <param name="xOffset" type="Number">x偏移量</param>
        /// <param name="yOffset" type="Number">y偏移量</param>
        /// <param name="xSize" type="Number">待读取数据x方向宽度</param>
        /// <param name="ySize" type="Number">待读取数据y方向宽度</param>
        /// <param name="buffer" type="TypeArray">保存读取结果的数组</param>
        /// <param name="dataType" type="enumDataType">数据类型</param>
        /// <param name="xBufferSize" type="Number">保存读取结果的数组buffer的x方向宽度</param>
        /// <param name="yBufferSize" type="Number">保存读取结果的数组buffer的y方向宽度</param>

        this.CheckArgumentsisValid(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize);
        this.DirectRead(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize);
    }
