var BinaryReader = require('../../Util/BinaryReader');
var MeteorologicalData = require('./MeteorologicalData');
var getString = require('../../Util/getString');



/**
 * 
 * @constructor
 * @memberof MeteoLib.Data.Micaps
 * @extends MeteoLib.Data.Micaps.MeteorologicalData
 *
 * @property {Boolean}isEnsemble 该网格是否是集合预报数据
 * @property {String}element  物理量：字节型50字节。应优先采用标准化字典表中的名称，建议采用全大写字母表示物理量名称，不建议使用汉字，否则会影响以后的字典标准化。不足50字节用“0”字节补齐。
 * @property {Number}description  附加描述信息：字节型30字节，用于表示附加描述，如区域范围，物理量单位等，建议使用字母，不建议使用汉字，如必须使用汉字，则采用GBK编码，不足30字节用“0”字节补齐。特别注意，该字段只用以表示附加描述，模式名、物理量、层次、起报日期、起报时刻、时效信息不得写入此字段中，否则会影响客户端对于数据描述的自动生成。
 * @property {Number}level  层次：代表模式数据高度对应的压强值，float型4字节。
 * @property {Number}year 起报日期：年：采用完整的整数表示，如2014，int型4字节。
 * @property {Number}month 起报日期：月：int型4字节。
 * @property {Number}day 起报日期：日：int型4字节。
 * @property {Number}hour 起报时刻：采用24小时制，数值范围0-23，int型4字节。
 * @property {Number}period 预报时效：单位小时，int型4字节。
 * @property {Number}startLongitude 起始经度：float型4字节。
 * @property {Number}endLongitude 终止经度：float型4字节。
 * @property {Number}longitudeGridSpace 经度格距：非0浮点数，float型4字节。
 * @property {Number}latitudeGridNumber 纬向经线格点数：int型4字节，代表一个纬线圈上经度格点的数量，应近似等于( endLongitude - startLongitude ) / longitudeGridSpace + 1。
 * @property {Number}startLatitude 起始纬度：float型4字节。
 * @property {Number}endLatitude 终止纬度：float型4字节。
 * @property {Number}latitudeGridSpace 纬度格距：非0浮点数，float型4字节。
 * @property {Number}longitudeGridNumber 经向纬线格点数：int型4字节，代表一个经线圈上纬度格点的数量。应近似等于( endLatitude - startLatitude ) / latitudeGridSpace + 1。latitudeGridNumber * longitudeGridNumber = 网格点数据个数
 * @property {Number}isolineStartValue 等值线起始值：float型4字节。当type为4时（标量网格数据），代表客户端进行等值线分析的起始数值，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
 * @property {Number}isolineEndValue 等值线终止值：float型4字节。当type为4时（标量网格数据），代表客户端进行等值线分析的终止数值，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
 * @property {Number}isolineSpace 等值线间隔：float型4字节。当type为4时（标量网格数据），代表等值线公差间隔，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
 * @property {Number}perturbationNumber 集合预报成员编号,如果是集合预报数据,则该字段占用扩展段前2字节
 * @property {Number}ensembleTotalNumber 集合预报成员总数,如果是集合预报数据，则该字段占用扩展段3-4字节
 * @property {Array.<Number>}dataArray  对于标量数据，代表网格数值，对于向量数据，代表向量的模
 * @property {Array.<Number>}angleArray 当数据是向量时，用来表示网格各点的角度值，数据是角度制，西风是0度，南风是90度，东风是180度，北风是270度
 * @example
 * 
 * 
    var GDSClient = MeteoLib.Data.Micaps.GDSClient;
 
    GDSClient.config.gdsServerIp = "xxx";
    GDSClient.config.gdsServerPort = "xx";
 
    var gdsClient = new GDSClient({
        proxy: {
            getUrl: function (srcUrl) {
                return   "http://xxxxxx/proxy/" + srcUrl;
            }
        }
    }); 
    gdsClient.getLatestDataName("ECMWF_HR/TMP/850", "*.024").then(function (strResult) {
    console.log(strResult)
    gdsClient.getData("ECMWF_HR/TMP/850", strResult.data).then(function (bytesResult) {
 
        var gridData = new GridData();
        gridData.loadByteArray(bytesResult.data);
        console.log(gridData)
    })
})
 */
function GridData() {

    this.isEnsemble = false;//该网格是否是集合预报数据 
    //大部分域变量均对应最终MICAPS网格数据格式中的数据头字段和数据区字段，字节数组总长度为数据头278字节+数据区（网格点数*float4字节，对于矢量网格还要再乘以2，因为还包括角度数组）

    //数据头开始，总共278字节
    this.type = GridData.SCALAR;//数据类型：short型2字节，4为模式标量数据，11为模式矢量数据，与原系统diamond 4和diamond 11含义一致。

    //首先是模式名称：字节型20字节，建议采用全大写字母表示模式名称，不建议使用汉字。不足20字节用“0”字节补齐。
    this.element = "";//物理量：字节型50字节。应优先采用标准化字典表中的名称，建议采用全大写字母表示物理量名称，不建议使用汉字，否则会影响以后的字典标准化。不足50字节用“0”字节补齐。
    this.description = "";//附加描述信息：字节型30字节，用于表示附加描述，如区域范围，物理量单位等，建议使用字母，不建议使用汉字，如必须使用汉字，则采用GBK编码，不足30字节用“0”字节补齐。特别注意，该字段只用以表示附加描述，模式名、物理量、层次、起报日期、起报时刻、时效信息不得写入此字段中，否则会影响客户端对于数据描述的自动生成。
    this.level = 0;//层次：代表模式数据高度对应的压强值，float型4字节。

    this.year;//起报日期：年：采用完整的整数表示，如2014，int型4字节。
    this.month;//起报日期：月：int型4字节。
    this.day;//起报日期：日：int型4字节。
    this.hour;//起报时刻：采用24小时制，数值范围0-23，int型4字节。
    //不要忘记还继承了超类MeteorologicalData的timezone，int型4字节，表示时区
    this.period;//预报时效：单位小时，int型4字节。

    this.startLongitude;//起始经度：float型4字节。
    this.endLongitude;//终止经度：float型4字节。
    this.longitudeGridSpace;//经度格距：非0浮点数，float型4字节。
    this.latitudeGridNumber;//纬向经线格点数：int型4字节，代表一个纬线圈上经度格点的数量，应近似等于( endLongitude - startLongitude ) / longitudeGridSpace + 1。
    this.startLatitude;//起始纬度：float型4字节。
    this.endLatitude;//终止纬度：float型4字节。
    this.latitudeGridSpace;//纬度格距：非0浮点数，float型4字节。
    this.longitudeGridNumber;//经向纬线格点数：int型4字节，代表一个经线圈上纬度格点的数量。应近似等于( endLatitude - startLatitude ) / latitudeGridSpace + 1。latitudeGridNumber * longitudeGridNumber = 网格点数据个数

    this.isolineStartValue;//等值线起始值：float型4字节。当type为4时（标量网格数据），代表客户端进行等值线分析的起始数值，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
    this.isolineEndValue;//等值线终止值：float型4字节。当type为4时（标量网格数据），代表客户端进行等值线分析的终止数值，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
    this.isolineSpace;//等值线间隔：float型4字节。当type为4时（标量网格数据），代表等值线公差间隔，当type为11时（矢量网格数据），此字段应忽略，建议设置为全0字节。
    //数据头最后是100字节的扩展段Extent,用于未来扩展，不用时建议设置为全0字节。扩展段后紧接数据区。
    this.perturbationNumber;//集合预报成员编号,如果是集合预报数据,则该字段占用扩展段前2字节
    this.ensembleTotalNumber;//集合预报成员总数,如果是集合预报数据，则该字段占用扩展段3-4字节
    //数据头结束

    //数据区开始
    /*数据区为所有网格数据的紧密排列，每个网格数据都是float型4字节，因此数据区总大小为latitudeGridNumber×longitudeGridNumber*4(矢量网格等于latitudeGridNumber*longitudeGridNumber*4*2)。
     * 排列顺序为先纬向后经向，即先从起始纬度开始，沿起始纬线圈扫描各点（从起始经度到终止经度），接着扫描下一个纬线圈，直至终止纬度。*/
    this.dataArray = []; //对于标量数据，代表网格数值，对于向量数据，代表向量的模
    this.angleArray = []; //当数据是向量时，用来表示网格各点的角度值，数据是角度制，西风是0度，南风是90度，东风是180度，北风是270度
    //数据区结束

}
GridData.DISCRIMINATOR = "mdfs";//合法数据关键字：始终为小写的mdfs，字符型4字节。不以mdfs开头的数据为非法数据

GridData.MODEL_NAME_LENGTH = 20;//模式名最大长度
GridData.ELEMENT_LENGTH = 50;//物理量最大长度
GridData.DESCRIPTION_LENGTH = 30;//网格数据附加描述信息最大长度
GridData.SCALAR = 4;//标量网格
GridData.VECTOR = 11;//矢量网格
GridData.CHARSET_NAME = "GB2312";

GridData.prototype = new MeteorologicalData();

/**
 * 
 * @param {Uint8Array|ArrayBuffer} dataBytes
 * @return {Boolean}
 */
GridData.prototype.loadByteArray = function (dataBytes) {
    //将字节数组映象转成GridData对象
    var i = 0;
    if (dataBytes instanceof ArrayBuffer) {
        dataBytes = new Uint8Array(dataBytes);
    }
    if (dataBytes == null || dataBytes.length < 278)//数据头
    {
        console.log("数据字节数不合要求");
        return false;
    }
    if (dataBytes[0] != 109 || dataBytes[1] != 100 || dataBytes[2] != 102 || dataBytes[3] != 115)//不以"mdfs"开头
    {
        console.log("尝试读取不以mdfs开头的字节数据");
        return false;
    }

    var br = new BinaryReader(dataBytes);
    br.seek(4, BinaryReader.SeekOrigin.Current);
    br.isLittleEndian = true;//设置为小端字节序

    //此3字段无法从字节数组中获得
    this.dataPath = "";
    this.dataName = "";
    this.originalAbsoluteDataName = "";

    //读取数据头
    this.type = br.readShort();//数据类型
    if (this.type != GridData.SCALAR && this.type != GridData.VECTOR)//非法类型
    {
        console.log("遇到非法类型: " + this.type);
        return false;
    }
    try {
        var modelNameByteArray = br.readBytes(GridData.MODEL_NAME_LENGTH);//模式名
        this.familyName = getString(modelNameByteArray, GridData.CHARSET_NAME);//将字节转换为UTF8字符串
        if (this.familyName) {
            this.familyName = String.deleteNull(this.familyName)
        }

        var elementByteArray = br.readBytes(GridData.ELEMENT_LENGTH);//物理量 
        this.element = getString(elementByteArray, GridData.CHARSET_NAME); //将字节转换为UTF8字符串
        if (this.element) {
            this.element = String.deleteNull(this.element);

        }
        var descriptionByteArray = br.readBytes(GridData.DESCRIPTION_LENGTH);//数据描述信息 
        this.description = getString(descriptionByteArray, GridData.CHARSET_NAME);  //将字节转换为UTF8字符串
        if (this.description) {
            this.description = String.deleteNull(this.description);
        }
    } catch (e) {
        console.log("将GBK字节转换为Unicode时出错");
        return false;
    }

    this.level = br.readFloat();//层次值

    this.year = br.readInt();//起报年
    this.month = br.readInt();//起报月
    this.day = br.readInt();//起报日
    this.hour = br.readInt();//起报时
    this.timezone = br.readInt();//数据时区
    this.period = br.readInt();//预报时效

    this.startLongitude = br.readFloat();//起始经度
    this.endLongitude = br.readFloat();//终止经度
    this.longitudeGridSpace = br.readFloat();//经度格距
    this.latitudeGridNumber = br.readInt();//纬向格点数
    this.startLatitude = br.readFloat();//起始纬度
    this.endLatitude = br.readFloat();//终止纬度
    this.latitudeGridSpace = br.readFloat();//纬度格距
    this.longitudeGridNumber = br.readInt();//经向格点数

    this.isolineStartValue = br.readFloat();
    this.isolineEndValue = br.readFloat();
    this.isolineSpace = br.readFloat();

    this.perturbationNumber = br.readShort();//扰动编号
    this.ensembleTotalNumber = br.readShort();//集合预报成员总数
    if (this.ensembleTotalNumber == 0 && this.perturbationNumber == 0)//利用这两个字段是否全0作为集合预报数据的充要条件
        this.isEnsemble = false;
    else
        this.isEnsemble = true;
    var extentByteArray = br.readBytes(96);
    extentByteArray = [];//略过扩展段

    //读取数据头完成，开始读取数据区
    if (this.latitudeGridNumber * this.longitudeGridNumber <= 0)//不应出现
    {
        console.log("非法纬向格点数或经向格点数, latitudeGridNumber = " + this.latitudeGridNumber + " longitudeGridNumber = " + this.longitudeGridNumber);
        return false;
    }
    this.dataArray = br.readFloats(this.latitudeGridNumber * this.longitudeGridNumber);//new float[latitudeGridNumber * longitudeGridNumber];

    if (this.type == GridData.VECTOR)//向量数据还需要读取角度
    {
        this.angleArray = br.readFloats(this.latitudeGridNumber * this.longitudeGridNumber); //new float[latitudeGridNumber * longitudeGridNumber];
    }

    //

    const gridData = this;
    var width = gridData.latitudeGridNumber,
        height = gridData.longitudeGridNumber;

    var bbox = [0, 0, 0, 0];

    var endLon = gridData.startLongitude + width * gridData.longitudeGridSpace;

    if (endLon < 180) {
        bbox[0] = gridData.startLongitude;
        bbox[2] = gridData.endLongitude;
    } else {
        bbox[0] = 180 - endLon;
        bbox[2] = 180;
    }
    bbox[1] = gridData.startLatitude < gridData.endLatitude ? gridData.startLatitude : gridData.endLatitude;
    bbox[3] = gridData.startLatitude > gridData.endLatitude ? gridData.startLatitude : gridData.endLatitude;
    this.bbox = bbox;
    this.width = width
    this.height = height

    //Micaps全球数据的经向是从0度到360度存储，而我们贴图的经度是从-180度到180度
    //所以我们需要处理180度到360度的部分
    if (endLon > 180) {
        var width = gridData.latitudeGridNumber,
            height = gridData.longitudeGridNumber;
        var newDataArray = new Float32Array(gridData.dataArray.length);
        var newAngleArray, isVector = gridData.type == GridData.VECTOR;
        if (isVector) {
            newAngleArray = new Float32Array(gridData.dataArray.length);
        }

        var idx = 0;
        for (var i = 0; i < height; i++) {
            for (var j = 0; j < width; j++) {
                var destIdx = i * width + j + width / 2;
                if (j > width / 2) {
                    destIdx = i * width + j - width / 2;
                }
                newDataArray[destIdx] = gridData.dataArray[idx];
                if (isVector) {
                    newAngleArray[destIdx] = gridData.angleArray[idx];
                }
                idx++;
            }
        }

        gridData.dataArray = newDataArray;
        if (isVector) {
            gridData.angleArray = newAngleArray;
        }

    }

    return true;
}//end method

module.exports = GridData;

