var ExtendMethod=require('../../Util/ExtendMethod');
var AwxHeaderBase=require('./AwxHeaderBase');
var DmrUtil=require('./DmrUtil');
var enumDataType=require('../RasterData/enumDataType');
var enumInterleave=require('../RasterData/enumInterleave');
var CoordEnvelope=require('../RasterData/CoordEnvelope');
var defineProperties=require('../../Util/defineProperties')

        /**
         *
         *@param {ArrayBuffer}headBuffer
         *@param {String}AwxRasterHeader
         *@property {Float32Array|Array.<Number>}CalTable
         *@constructor  
         *@memberof MeteoLib.Data.Awx 
         *@extends MeteoLib.Data.Awx.AwxHeaderBase
         */
        function AwxRasterHeader(headBuffer, awxFileName) {
            if (arguments.length > 0) {
                this._init(headBuffer, awxFileName);
                this._attributes = AwxRasterHeader.readhead(headBuffer);

                var dblength = parseInt(this._attributes["定标数据块长度"]);
                var flag = parseInt(this._attributes["flag"]);
                this._calTable = AwxRasterHeader.readdb(headBuffer, dblength, flag);

                this._channelCount = 1;
                this._width = parseInt(this._attributes["图像宽度"].toString());
                this._height = parseInt(this._attributes["图像高度"].toString());
                this._bandNames = this.getAllBandName();
                this._bandName = this._bandNames[0];
                this._resolutionX=this._attributes["投影水平分辨率"];
                this._resolutionY=this._attributes["投影垂直分辨率"];
                this._centerY=this._attributes["投影中心纬度"];;
                this._centerX=this._attributes["投影中心经度"];

                if (this._bandName != "VIS" && this._bandName != "VIS1KM") {
                    this._dataType = enumDataType.Float;
                }
                else {
                    this._dataType = enumDataType.Byte;
                }
            }
        }
        AwxRasterHeader.prototype = new AwxHeaderBase();
        defineProperties(AwxRasterHeader.prototype, {
            CalTable: {
                get: function () { return this._calTable; }
            }
        });
        /**
     *
     *@return {AwxGridHeader}
     */
        AwxRasterHeader.prototype.Clone = function () {
            var header = new AwxRasterHeader();
            for (var i in this) {
                if (this.hasOwnProperty(i)) {
                    header[i] = this[i];
                }
            }
            return header;
        }
        AwxRasterHeader.prototype.getAllBandName = function () {
            var band = null;
            var up = this._fileName.toUpperCase();
            if (up.indexOf("VIS") >= 0) {
                band = "VIS";
                if (up.indexOf("PJ2") >= 0) {
                    band = "VIS1KM";
                }
                return [band];
            }
            if (up.indexOf("IR4") >= 0) {
                band = "IR4";
                return [band];
            }
            if (up.indexOf("IR1") >= 0) {
                band = "IR1";
                return [band];
            }
            if (up.indexOf("IR2") >= 0) {
                band = "IR2";
                return [band];
            }
            if (up.indexOf("IR1") >= 0) {
                band = "IR1";
                return [band];
            }
            band = this._attributes["通道号"].toString();
            return [band];
        }

        AwxRasterHeader.IsAwx = function (header4096, fileExtension) {
            var attributes = AwxRasterHeader.readhead(header4096);
            var productClass = attributes["产品类别"];
            if (productClass == null) return false;
            var ret = fileExtension.trim('.') == "AWX" || fileExtension == ".AWX";
            ret = ret && productClass.toString() == "1";
            return ret;
        }

        /// <summary>
        /// 定标（将计数值转为实际物理量），返回结果缩放倍数为1，并存储为float数据类型
        /// </summary>
        /// <param name="data"></param>
        /// <returns></returns>
        AwxRasterHeader.prototype.ChangeToFloat = function (srcDataArray, dstDbArray) {
            if (this.CalTable == null) {
                dstDbArray = null;
                return false;
            }
            if (this.CalTable.length <= 0) {
                dstDbArray = null;
                return false;
            }
            if (dstDbArray == null) {
                dstDbArray = new Float32Array(srcDataArray.length);
            }
            else if (dstDbArray.length == 0) {
                dstDbArray = new float[srcDataArray.length];
            }

            for (var j = 0; j < srcDataArray.length; j++) {
                dstDbArray[j] = parseFloat(this.CalTable[((srcDataArray[j] & 0xFF) * 4)]);
            }
            return true;
        }

        /// <summary>
        /// 定标（将计数值转为实际物理量），返回结果缩放倍数为10，并存储为short数据类型
        /// </summary>
        /// <param name="data"></param>
        /// <returns></returns>
        AwxRasterHeader.prototype.ChangeToShort = function (srcDataArray, dstDbArray) {
            if (this.CalTable == null) {
                dstDbArray = null;
                return false;
            }
            if (this.CalTable.length <= 0) {
                dstDbArray = null;
                return false;
            }
            if (dstDbArray == null) {
                dstDbArray = new Int8Array(srcDataArray.length);
            }
            else if (dstDbArray.length == 0) {
                dstDbArray = new Int8Array(srcDataArray.length);
            }

            for (var j = 0; j < srcDataArray.length; j++) {
                dstDbArray[j] = parseInt(this.CalTable[((srcDataArray[j] & 0xFF) * 4)] * 10);
            }
            return true;
        }

        /**
         *
         * @param {Uint8Array|Array.<Number>}bs
         * @returns {Object}
         */
        AwxRasterHeader.readhead = function (bs) {
            var prop = {};
            AwxHeaderBase.readOneHead(bs, prop);
            var s40 = String.fromCharCode(bs[40] & 0xFF);
            var s41 = String.fromCharCode(bs[41] & 0xFF);
            var s42 = String.fromCharCode(bs[42] & 0xFF);
            var s43 = String.fromCharCode(bs[43] & 0xFF);
            var s44 = String.fromCharCode(bs[44] & 0xFF);
            var s45 = String.fromCharCode(bs[45] & 0xFF);
            var s46 = String.fromCharCode(bs[46] & 0xFF);
            var s47 = String.fromCharCode(bs[47] & 0xFF);

            var ss = s40 + s41 + s42 + s43 + s44 + s45 + s46 + s47;
            var flag = DmrUtil.bytesToShort(bs[12], bs[13]);

            var s49 = DmrUtil.bytesToShortByflag(bs[48], bs[49], flag);
            var s51 = DmrUtil.bytesToShortByflag(bs[50], bs[51], flag);
            var s53 = DmrUtil.bytesToShortByflag(bs[52], bs[53], flag);
            var s55 = DmrUtil.bytesToShortByflag(bs[54], bs[55], flag);
            var s57 = DmrUtil.bytesToShortByflag(bs[56], bs[57], flag);
            var s59 = DmrUtil.bytesToShortByflag(bs[58], bs[59], flag);
            var s61 = DmrUtil.bytesToShortByflag(bs[60], bs[61], flag);
            var s63 = DmrUtil.bytesToShortByflag(bs[62], bs[63], flag);
            var s65 = DmrUtil.bytesToShortByflag(bs[64], bs[65], flag);

            var s73 = DmrUtil.bytesToShortByflag(bs[72], bs[73], flag);
            var s75 = DmrUtil.bytesToShortByflag(bs[74], bs[75], flag);
            var s77 = DmrUtil.bytesToShortByflag(bs[76], bs[77], flag);
            var s79 = DmrUtil.bytesToShortByflag(bs[78], bs[79], flag);

            var s89 = DmrUtil.bytesToShortByflag(bs[88], bs[89], flag) / 100.0;
            var s91 = DmrUtil.bytesToShortByflag(bs[90], bs[91], flag) / 100.0;
            var s81 = DmrUtil.bytesToShortByflag(bs[80], bs[81], flag) / 100.0;
            var s83 = DmrUtil.bytesToShortByflag(bs[82], bs[83], flag) / 100.0;
            var s93 = DmrUtil.bytesToShortByflag(bs[92], bs[93], flag);
            var s95 = DmrUtil.bytesToShortByflag(bs[94], bs[95], flag);
            var s97 = DmrUtil.bytesToShortByflag(bs[96], bs[97], flag);
            var s99 = DmrUtil.bytesToShortByflag(bs[98], bs[99], flag);
            var s101 = DmrUtil.bytesToShortByflag(bs[100], bs[101], flag);

            prop["年"] = s49;
            prop["月"] = s51;
            prop["日"] = s53;
            prop["时"] = s55;
            prop["分"] = s57;
            prop["通道号"] = s59;
            prop["投影方式"] = s61;
            prop["图像宽度"] = s63;
            prop["图像高度"] = s65;

            var b = (s73 > 180) || (s75 > 180) || (s77 > 180) || (s79 > 180);
            if (b) {
                prop["北纬"] = s73 / 100.0;
                prop["南纬"] = s75 / 100.0;
                prop["西经"] = s77 / 100.0;
                prop["东经"] = s79 / 100.0;
            }
            else {
                prop["北纬"] = s73;
                prop["南纬"] = s75;
                prop["西经"] = s77;
                prop["东经"] = s79;
            }

            if (prop['北纬'] > 90 || prop['北纬'] < -90) {
                prop['北纬'] = s81 + s91 * (s65 / 2)
            }
            if (prop['南纬'] > 90 || prop['南纬'] < -90) {
                prop['南纬'] = s81 - s91 * (s65 / 2)
            }

            if (prop['东经'] > 180 || prop['东经'] < -180) {
                prop['东经'] = s83 + s89 * (s63 / 2)
            }
            if (prop['西经'] > 180 || prop['西经'] < -180) {
                prop['西经'] = s83 - s89 * (s63 / 2)
            }

            prop["投影中心纬度"] = s81;
            prop["投影中心经度"] = s83;
            prop["投影水平分辨率"] = s89;
            prop["投影垂直分辨率"] = s91;
            prop["地理网格叠加标志"] = s93;
            prop["地理网格叠加值"] = s95;
            prop["定标数据块长度"] = s99;
            prop["调色表数据块长度"] = s97;
            prop["定位数据块长度"] = s101;

            return prop;
        }


        AwxRasterHeader.readdb = function (bs, dblength, flag) {
            /// <summary>
            /// 读取定标数据块
            /// </summary>
            /// <param name="bs"></param>
            /// <param name="dblength"></param>
            /// <param name="flag"></param>
            /// <returns></returns>

            var dbindex = 104;
            var size = dblength / 2;
            var db = new Float32Array(size);
            if (flag == 0) {
                for (var i = 0; i < size; i++) {
                    db[i] = (AwxRasterHeader.bytesToUnsigned(bs[(dbindex + i * 2 + 1)], bs[(dbindex + i * 2 + 0)]) * 0.01);
                }
            }
            return db;
        }
        AwxRasterHeader.bytesToUnsigned = function (b0, b1) {
            return ((b0 & 0xFF) << 8) + (b1 & 0xFF);
        }
        /**
     *
     */
        AwxRasterHeader.prototype.Dispose = function () {
            this._dataType = null;
            this._interleave = null;
            this._channelCount = null;
            this._width = null;
            this._height = null;
            this._isExtended = null;
            this._isDay = null;
            this._attributes = null;
            this._coordEnvelope = null;
            this._resolutionX = null;
            this._resolutionY = null;
            this._bandNames = null;
            this._fileName = null;
            this._bandName = null;
            this._calTable = null;
            return null;
        }

        /**
      *
      */
        AwxRasterHeader.prototype.destroy = function () {
            return this.Dispose();
        }
        module.exports= AwxRasterHeader;
    